header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}
/* *************CV************ */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
/* *************HEADERSOCIALS************ */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position:absolute;
    left: 0;
    bottom: 3rem;
}
.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
/* *************ME************ */
.me{
    background: linear-gradient(var(--color-primary),transparent) ;
    width: 22rem;
    height: 22rem;
    position: relative;
    left:50%;
    transform:translateX(-50%) ;
    margin-top: 4rem;
    border-radius: 50%;
    overflow: hidden;
    
}

/* *************SCROLL DOWN************ */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
/* ***********MEDIA QUERIES(Medium Devices)************ */
@media screen  and (max-width:1024px){
    header{
        height: 68vh;
    }
}
/* ***********MEDIA QUERIES(Small Devices)************ */
@media screen  and (max-width:600px){
    header{
        height: 100vh;
        width:100%;
    }
    .header__socials,
    .scroll__down{
        display: none;
    }
}

